import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import TableDropoutList from './TableDropoutList';
import SearchBar from './SearchBar';
import './style.scss'


class DropoutListlPage extends Component {
  state = {};

  render() {

    return (
      <Card title="Danh sách thôi, ngừng học">
        <SearchBar />
        <Row>
          <Col span={24}>
            <BtnAddNew title="Nhập xét học tiếp" href="#/studentProfile/DropoutList/from" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDropoutList />
          </Col>
        </Row>
      </Card >
    );
  }
}

export default props => (
  <Page {...props}>
    <DropoutListlPage />
  </Page>
);



