import React, { Component, Fragment } from 'react';
import { Row, Col, Form, Select, Button, DatePicker, Input, } from 'antd';
import moment from 'moment';

const { Option } = Select;

const dateFormat = 'YYYY/MM/DD';


@Form.create({ name: 'formScholarshipApproval' })


class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={8} >
            <Form.Item label='Hệ'>
              {getFieldDecorator('system1', {
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khoa'>
              {getFieldDecorator('system2', {
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khóa'>
              {getFieldDecorator('system3', {
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='CN'>
              {getFieldDecorator('system4', {
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Học kỳ'>
              {getFieldDecorator('system5', {
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Năm học'>
              {getFieldDecorator('system6', {
              })(
                <Fragment>
                  <DatePicker defaultValue={moment('2015/01/01', dateFormat)} format={dateFormat} />
                </Fragment>)
              }
            </Form.Item>
          </Col>

          <Col span={8} >
            <Form.Item label='Mã sinh viên'>
              {getFieldDecorator('system11', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Họ tên'>
              {getFieldDecorator('system12', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Loại quyết định'>
              {getFieldDecorator('system13', {
              })(
                <Select>
                  <Option value="1">Quyết định 1</Option>
                  <Option value="2">Quyết định 2</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">Liệt kê danh sách</Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
              Xóa
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar



