import React, { Component, Fragment } from 'react';
// import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Table, Popconfirm, Checkbox } from 'antd';
import { DropoutList } from 'dump/data';



const columns = [
  {
    title: 'Chọn',
    dataIndex: 'nameRating',
    render: () =>
      DropoutList.length >= 1 ? (
        <Fragment>
          <Checkbox />
        </Fragment>
      ) : null,
  },
  {
    title: 'Số QĐ',
    dataIndex: 'number',
    key: 'number',
  },
  {
    title: 'Ngày QĐ',
    dataIndex: 'date',
    key: 'date',
  },
  {
    title: 'Nội dung',
    dataIndex: 'content',
    key: 'content',
  },
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp cũ',
    dataIndex: 'oldClass',
    key: 'oldClass',
  },
  {
    title: 'Lớp mới',
    dataIndex: 'newClass',
    key: 'newClass',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      DropoutList.length >= 1 ? (
        <Fragment>
          {/* <BtnEdit href={`#DropoutList/form/edit/${record.codeRating}`} /> */}
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class TableDropoutList extends Component {
  state = {};

  render() {

    return (
      <Table bordered dataSource={DropoutList} columns={columns} />
    );
  }
}

export default TableDropoutList;



